import Vue from 'vue'
import router from '@/router'
import store from '@/store'

/**
 * 获取uuid
 */
export function getUUID () {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    return (c === 'x' ? (Math.random() * 16 | 0) : ('r&0x3' | '0x8')).toString(16)
  })
}

/**
 * 是否有权限
 * @param {*} key
 */
export function isAuth (key) {
  return JSON.parse(sessionStorage.getItem('permissions') || '[]').indexOf(key) !== -1 || false
}

/**
 * 树形数据转换
 * @param {*} data
 * mainKey 节点标识
 * @param {*} id
 * @param {*} parentId
 */
export function treeDataTranslate (data,mainKey,id,parentId,children) {
  let cloneData=JSON.parse(JSON.stringify(data))// 对源数据深度克隆
  return cloneData.filter(father=>{// 循环所有项，并添加children属性
  	let branchArr=cloneData.filter(child => father[id] == child[parentId]); // 返回每一项的子级数组
  	if(branchArr.length>0){//给父级添加一个children属性，并赋值
  		father[children]=branchArr;
  	}
  	return father[parentId]==mainKey;//返回第一层
  })
}

/**
 * 清除登录信息
 */
export function clearLoginInfo () {
  sessionStorage.removeItem('token')
  sessionStorage.removeItem('navSel')
  store.commit('resetStore')
  router.options.isAddDynamicMenuRoutes = false
}

/* 上移下移置顶置低
	*arr是要操作的数组
	*index1 是准备移动的元素
	*index2 是准备移动到的位置
  *direction 移动
	*/
export function	swapItems(arr,index1,index2,direction) {
		if(direction=='top'){//置顶
			arr.unshift(arr[index1]);
			arr.splice(index1+1,1);
			return arr;
		}else if(direction=='bottom'){//置底
			arr.push(arr[index1]);
			arr.splice(index1,1);
			return arr;
		}else{
			arr[index1]=arr.splice(index2,1,arr[index1])[0];
			return arr;
		}
	}

	export function arrYear(oldyear){
    var myDate = new Date();
    var thisYear = myDate.getFullYear();  // 获取当年年份
    var Section = thisYear - oldyear;  // 声明一个变量 获得当前年份至想获取年份差 eg.2008
    var arrYear = []; // 声明一个空数组 把遍历出的年份添加到数组里
    for(var i = 0;i<=Section;i++){
      arrYear.push(thisYear--)
    } // 遍历并添加年份到数组
    // arrYear里便是2008年到当前的所有年份了
    return arrYear
  }



