export default {
  namespaced: true,
  state: {
    id: 0,
    name: '',
    areaId:'',
  },
  mutations: {
    updateId (state, id) {
      state.id = id
    },
    updateName (state, name) {
      state.name = name
    },
    updateAreaId(state,areaId){
      state.areaId=areaId
    }
  }
}



