/**
 * 全站路由配置
 *
 * 建议:
 * 1. 代码中路由统一使用name属性跳转(不使用path属性)
 */
import Vue from 'vue'
import Router from 'vue-router'
import http from '@/utils/httpRequest'
import {isURL} from '@/utils/validate'
import {clearLoginInfo} from '@/utils'
import API from '../api/api.js'
import store from '@/store'

Vue.use(Router)
//获取原型对象上的push函数
const originalPush = Router.prototype.push
//修改原型对象中的push方法
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const _import=require('./import-'+process.env.NODE_ENV)

// 全局路由(无需嵌套上左右整体布局)
const globalRoutes = [
  { path: '/404', component: _import('common/404'), name: '404', meta: { title: '404未找到' } },
  { path: '/login', component: _import('common/login'), name: 'login', meta: { title: '登录' } },
  { path: '/loginPlatConfig', component: _import('common/loginPlatConfig'), name: 'loginPlatConfig', meta: { title: '配置登录' } },
  { path: '/platHome', component: _import('platConfigView/platConfigIndex'), name: 'platHome', meta: { title: '配置首页' },
    children: [
      {path: '/configInfor', component: _import('platConfigView/configContent/configInfor'), name: 'configInfor', meta: { title: '信息一部' }}
    ]
  },
]


// 主入口路由
const mainRoutes = {
  path: '/',
  component: _import('main/index'),
  name: 'main',
  redirect: { name: 'home' },
  meta: { title: '主入口整体布局' },
  children: [
    // 通过meta对象设置路由展示方式
    // 1. isTab: 是否通过tab展示内容, true: 是, false: 否
    // 2. iframeUrl: 是否通过iframe嵌套展示内容, '以http[s]://开头': 是, '': 否
    // 提示: 如需要通过iframe嵌套展示内容, 但不通过tab打开, 请自行创建组件使用iframe处理!
    { path: '/home', component: _import('homepage/index'), name: 'home', meta: { title: '首页' } },

  ],
  beforeEnter (to, from, next) {
    let token = JSON.parse(sessionStorage.getItem('token'))
    if (!token || !/\S/.test(token)) {
      clearLoginInfo()
      next({ name: 'login' })
    }
    next()
  }
}

const createRouter=() => new Router({
  mode: 'history',
  base:window.__POWERED_BY_QIANKUN__ ? '/rmsh' : '/',
  scrollBehavior: () => ({ y: 0 }),
  isAddDynamicMenuRoutes: false, // 是否已经添加动态(菜单)路由
  routes: globalRoutes.concat(mainRoutes)
})

const router = createRouter()

export function resetRouter() {
  const newRouter=createRouter()
  router.matcher=newRouter.matcher
}

router.beforeEach((to, from, next) => {

  if (router.options.isAddDynamicMenuRoutes || fnCurrentRouteType(to, globalRoutes) === 'global') {
    next()
  } else {
    http({
      url:http.adornUrl(API.getUserInfo),
      method: 'post',
      params: http.adornParams()
    }).then(({data}) => {
      if (data && data.code === 0) {
        sessionStorage.setItem('getUserInfo', JSON.stringify(data.data || '[]'))
        getMenuList(data.data,to, from, next)
      }
    })
  }
})
function getMenuList(user,to, from, next){//获取子系统和菜单

  // let commonState = store.state.common
  // let menuList = commonState.menuList
  // console.log('----------------------=============')
  // console.log(commonState)
  // console.log(typeof commonState)
  // console.log(menuList)
  // console.log(commonState['menuActiveName'])


  http({
    url:http.adornUrl(API.getMenuByUserAndSys),
    method: 'post',
    params: http.adornParams({
      areaId:user.areaId,
      relateType:'0',
      userId:user.userId
    })
  }).then(({data}) => {
    if (data && data.code === 0) {
      sessionStorage.setItem('navSystem', JSON.stringify(data.data.systemlist || '[]'))


      // let systemId =''
      // if(window.__POWERED_BY_QIANKUN__){
      //   systemId = sessionStorage.getItem('systemId')
      // }else{
      //   systemId = "AFD37065-3B50-4606-9878-0633CA456502"
      // }
      // let menuTreelist  = data.data.menuTreelist
      // let menuTreelistFilter =  menuTreelist.filter(item => item.sysId == systemId );
      // console.log(menuTreelistFilter)
      fnAddDynamicMenuRoutes(data.data.menuTreelist)
      // fnAddDynamicMenuRoutes(menuTreelistFilter)
      router.options.isAddDynamicMenuRoutes = true
      sessionStorage.setItem('menuList', JSON.stringify(data.data.menuTreelist || '[]'))
      next({ ...to, replace: true })
    }else {
      sessionStorage.setItem('navSystem', '[]')
      sessionStorage.setItem('menuList', '[]')
      next()
    }
  })
}
/**
 * 判断当前路由类型, global: 全局路由, main: 主入口路由
 * @param {*} route 当前路由
 */
function fnCurrentRouteType (route, globalRoutes = []) {
  var temp = []
  for (var i = 0; i < globalRoutes.length; i++) {
    if (route.path === globalRoutes[i].path) {
      return 'global'
    } else if (globalRoutes[i].children && globalRoutes[i].children.length >= 1) {
      temp = temp.concat(globalRoutes[i].children)
    }
  }
  return temp.length >= 1 ? fnCurrentRouteType(route, temp) : 'main'
}

/**
 * 添加动态(菜单)路由
 * @param {*} menuList 菜单列表
 * @param {*} routes 递归创建的动态(菜单)路由
 */
function fnAddDynamicMenuRoutes (menuList = [], routes = []) {

  var temp = []
  for (var i = 0; i < menuList.length; i++) {
    if (menuList[i].children && menuList[i].children.length >= 1) {
      temp = temp.concat(menuList[i].children)
    } else if (menuList[i].path && /\S/.test(menuList[i].path)) {
      menuList[i].path= menuList[i].path.replace(/^\//, '')
      var route = {
        path: menuList[i].path.replace('/', '-'),
        component: null,
        name: menuList[i].path.replace('/', '-'),
        meta: {
          menuId: menuList[i].id,
          title: menuList[i].name,
          isDynamic: true,
          isTab: true,
          iframeUrl: ''
        }
      }
      // url以http[s]://开头, 通过iframe展示
      if (isURL(menuList[i].path)) {
        route['path'] = `i-${menuList[i].id}`
        route['name'] = `i-${menuList[i].id}`
        route['meta']['iframeUrl'] = menuList[i].path
      } else {
        try {
          route['component'] = _import(`modules/${menuList[i].path}`) || null
        } catch (e) {}
      }
      routes.push(route)
    }
  }
  if (temp.length >= 1) {
    fnAddDynamicMenuRoutes(temp, routes)
  } else {
    mainRoutes.name = 'main-dynamic'
    mainRoutes.children = routes
    router.addRoutes([
      mainRoutes,
      { path: '*', redirect: { name: '404' } }
    ])

    sessionStorage.setItem('dynamicMenuRoutes', JSON.stringify(mainRoutes.children || '[]'))
    // if(!window.__POWERED_BY_QIANKUN__){
    //   sessionStorage.setItem('systemId',"4BC9E3A7-801D-4744-88BF-52D84540E0FE")
    // }
    // console.log('\n')
    // console.log('%c!<-------------------- 动态(菜单)路由 s -------------------->', 'color:blue')
    // console.log(mainRoutes.children)
    // console.log('%c!<-------------------- 动态(菜单)路由 e -------------------->', 'color:blue')
  }
}
console.log(mainRoutes)
console.log(router)
export default router



