import Vue from 'vue'
import App from '@/App'
import router from '@/router' // api: https://github.com/vuejs/vue-router
import store from '@/store' // api: https://github.com/vuejs/vuex
import VueCookie from 'vue-cookie' // api: https://github.com/alfhen/vue-cookie
import '@/element-ui' // api: https://github.com/ElemeFE/element
import '@/icons' // api: http://www.iconfont.cn/
import '@/element-ui-theme'
import '@/assets/scss/index.scss'
import httpRequest from '@/utils/httpRequest' // api: https://github.com/axios/axios
import { isAuth } from '@/utils'
import api from "@/api/api.js" //接口详情数据
import cloneDeep from 'lodash/cloneDeep'
import VueParticles from 'vue-particles'
import commonFun from '../src/common/commonFun'
//打印功能
import Print from 'vue-print-nb'
Vue.use(Print);
// 引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts

// collapse 展开折叠
import CollapseTransition from 'element-ui/lib/transitions/collapse-transition';
Vue.component(CollapseTransition.name, CollapseTransition)

//引入iconfont样式
import '@/assets/css/iconfont/iconfont.css';

Vue.use(VueParticles)
Vue.use(VueCookie)
Vue.config.productionTip = false

// 适配mockjs模拟数据 // api: https://github.com/nuysoft/Mock
// if (process.env.NODE_ENV !== 'production') {
//   require('@/mock')
// }

// 挂载全局
Vue.prototype.$http = httpRequest // ajax请求方法
Vue.prototype.API = api;
Vue.prototype.isAuth = isAuth // 权限方法
Vue.prototype.commonFun = commonFun
Vue.prototype.$host = process.env.NODE_ENV !== 'production' && process.env.OPEN_PROXY ? '/api/' : window.SITE_CONFIG.baseUrl

// 保存整站vuex本地储存初始状态
window.SITE_CONFIG['storeState']=cloneDeep(store.state)

/* eslint-disable no-new */


let instance = null
function render(props){
  instance =  new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
  })
}
if (window.__POWERED_BY_QIANKUN__) {
  __webpack_public_path__ = window.__INJECTED_PUBLIC_PATH_BY_QIANKUN__;
}
if (!window.__POWERED_BY_QIANKUN__) {
  render();
}
let  baseRouteronstract
export async function bootstrap(props){}
export async function mount(props){
  console.log(props)
  render(props)
  baseRouteronstract = props.router
  instance.$store.commit("common/updateSystemId",props.systemId);

}
export  function baseRouter () {
  return baseRouter
}
export async function unmount(props){
  instance.$destroy()
}



